<template>
  <div class="mine_container">
    <div class="top_header">
      <header-view :titles="title"></header-view>
    </div>
    <div class="car_list">
     <ul>
       <li class="car_list_item" v-for="(item ,index) in shopList" >
        <div class="shop_name_edit">
           <div class="left_shop">
             <span class="shop_icon"></span>
             <span class="shop_name">{{item.store_name}}</span>
           </div>
           <p class="edit">编辑</p>
        </div>
         <div class="buy_good">
           <div class="left_select" @click="selected(index)">
             <!--通过item中的select_status来判断是否选中，进一步来判断css的样式-->
             <div :class="(selectStatus[index].select_status===true)? 'cbselect' : 'cb_unselect'" ></div>
           </div>
           <img :src="item.goods_image_url" alt="" class="good_img">
           <div class="good_desc">
              <div class="good_name">
               {{item.goods_name}}
              </div>
              <div class="good_property">
                 <p class="one">商品的参数详情</p>
                 <p class="two">￥{{item.goods_price }} 元</p>
                <div class="fan_num">
                  <span>返现<span class="fan">{{item.goods_return_rate}}</span>元</span>
                  <span>x {{item.goods_num}}</span>
                </div>
              </div>
           </div>
         </div>
       </li>

     </ul>
    </div>

    <div class="total">
      <div class="all_select" @click="allSelect">
        <div  :class="(allSelected===true)? 'icon_select' : 'cb_unselect'"></div>
        <p>全选</p>
      </div>
      <div class="total_money">合计：$123.0</div>
      <div class="topay" @click="sumbitCart">结算</div>
    </div>
  </div>
</template>
<script>
  import HeaderView from '../../components/header-view'
  export default {
    data(){
      return{
        title:'购物车',
        shopList:[],
        sum:0,
        //用来保存被选中的数组index
        selectStatus:[],
        allSelected:false
      }
    },
    computed:{
    },
    components:{
      HeaderView
    },

    methods:{

      //全选 点击事件，关联到列表
      allSelect(){
        this.allSelected=!this.allSelected;
        if(this.allSelected){
          for(let i=0;i<this.selectStatus.length;i++){
              this.selectStatus[i].select_status=true;
          }
        }else{
          for(let i=0;i<this.selectStatus.length;i++){
            this.selectStatus[i].select_status=false;
          }
        }
      },

      //列表中的条目是否被勾选中 其中index为列表中的index
      selected(index){
        for(let i=0;i<this.selectStatus.length;i++){
            if(index===i){
              this.selectStatus[index].select_status=! this.selectStatus[index].select_status;
            }
        }
        //将列表中的数据遍历，将列表中的item是否被勾选中和全选的状态关联起来
        for(let i=0;i<this.selectStatus.length;i++){
          if(!this.selectStatus[i].select_status){
            this.allSelected=false;
            return;
          }
          this.allSelected=true
        }

      },
      //获取购物车商品列表
      getShopCar(){
        let token=localStorage.getItem('token');
        let that=this;
        this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=member_cart&op=cart_list&token="+token, //跨域请求接口
          {
            jsonp:'callback',
            jsonpcallback	:'cart',
          }).then(function(res){
          console.log('=====购物车token========'+JSON.parse(res.bodyText))
          that.shopList=JSON.parse(res.bodyText).datas.cart_list;
          that.sum=JSON.parse(res.bodyText).datas.sum;
          //遍历列表数组 ,创建一个新的数组，每一item中包含了一个对象 其中属性为 id 为 index 和选中的状态
          for (let i=0;i<that.shopList.length;i++){
            let itemStatus={
              id:i,
              //选中的状态
              select_status:false
            }
            that.selectStatus.push(itemStatus);
            console.log(that.selectStatus.length);
          }

        },function(){
          console.log("购物车请求失败！！！")
        });
      },

      //结算
      sumbitCart(){
        var cartIdStr=this.getCardParam();
        console.log("======cartIdStr==="+cartIdStr);


        let token=localStorage.getItem('token');
        const postData={
          cart_id: cartIdStr,
          ifcart: '1',
        }

        this.$http({
          url: 'gp/mobile/index.php?' + 'act=member_buy&op=buy_step1&token='+token+"&method=post",
          method: 'POST',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
          },
          data: postData
        })
          .then(res => {
            console.log(res);
          })
          .catch(err => {
            console.log(err);
          })
      },
       getCardParam() {
          let cartId=[];
             for(let i=0;i<this.selectStatus.length;i++){
                if(this.selectStatus[i].select_status){ //用户勾选上
                  cartId.push(this.shopList[i].cart_id+"|"+this.shopList[i].goods_num)
                }
             }
          return cartId.join(",")
        }
    },
    created(){
      //显示底部导航栏
      this.$store.commit("showFooterTab",true);
      this.getShopCar();
    }
  }
</script>
<style scoped>
@import "../../css/reset.css";
.mine_container{
  background-color: #f9f9f9;
}
.top_header{
  width: 100%;
  position: fixed;
  left: 0;
  top:0;
  z-index: 3;
}
  .car_list{
    margin-top: 51px;
    margin-bottom: 10.5rem;
    width: 100%;
  }
  .total{
    display: flex;
    width: 100%;
    height: 50px;
    position: fixed;
    left: 0;
    bottom: 54px;
    background-color: #f9f9f9;
    z-index: 3;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
  }
  .all_select{
    display: flex;
    margin-left: 1.5rem;
    align-items: center;
  }
  .icon_select{
     width: 24px;
     height: 24px;
    background-image: url("../../assets/images/cb_checked.png");
    background-repeat: no-repeat;
    background-size: 24px 24px;
  }
.all_select p{
  margin-left: 0.5rem;
  font-family: "SimSun", "宋体";
  color: #333333;
  font-size: 16px;
}
.total_money{
  font-family: "SimSun", "宋体";
  color: #333333;
  font-size: 16px;
  font-weight: 700;
}
  .topay{
    margin-right: 2rem;
    border: 0.1rem solid #0BA062;
    padding: 0.4rem 1.5rem;
    background-color: #0BA062;
    color: white;
    font-size: 16px;
  }
  .car_list_item{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 200px;
    background-color: #fff;
    margin-bottom: 0.5rem;
    padding-top: 1rem;
    box-sizing: border-box;
  }
  .shop_name_edit{
    display: flex;
    width: 100%;
    height: 4rem;
    background-color: #ffffff;
    box-sizing: border-box;
    padding: 0 1.5rem;
    justify-content: space-between;
    align-items: center;
    border-bottom: 0.1rem solid #f9f9f9;
  }
  .left_shop{
     display: flex;
     height: 100%;
     align-items: center;
  }
  .shop_icon{
    display: inline-block;
    width: 24px;
    height: 24px;
    background-image: url("../../assets/images/shop_icon.png");
    background-repeat: no-repeat;
    background-size: 24px 24px;
  }

  .shop_name{
    margin-left: 0.8rem;
    font-family: "SimSun", "宋体";
    font-size: 16px;
  }
  .edit{
    font-family: "SimSun", "宋体";
    font-size: 16px;
    color: #0BA062;
  }
  .buy_good{
    display: flex;
    flex: 1;
    width: 100%;
    align-items: center;
    box-sizing: border-box;
    padding: 0 1.5rem;
  }
  .left_select{
    display: flex;
    width: 4.4rem;
    height: 100%;
    align-items: center;
    justify-content: center;
  }
  .cbselect{
    width: 24px;
    height: 24px;
    background-image: url("../../assets/images/cb_select.png");
    background-repeat: no-repeat;
    background-size: 24px 24px;
  }
  .cb_unselect{
    width: 24px;
    height: 24px;
    background-image: url("../../assets/images/cb_unselect.png");
    background-repeat: no-repeat;
    background-size: 24px 24px;
  }
  .good_img{
    margin-left: 1.5rem;
    display: block;
    width: 100px;
    height: 100px;
  }
  .good_desc{
    display: flex;
    flex: 1;
    flex-direction: column;
    height: 100px;
    margin-left: 1rem;

  }
  .fan_num{
    display: flex;
    justify-content: space-between;

  }
  .fan{
    color: #0BA062;
  }
  .good_name{
    display: block;
    width: 100px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 30px;
    line-height: 30px;
    color: #333333;
    font-family: sans-serif;
    font-weight: 400;
    font-size: 16px;
  }
  .one{
    height: 20px;
    line-height: 20px;
    color: #666666;
    font-size: 16px;
    font-family: sans-serif;
  }
  .two{

    height: 20px;
    line-height: 20px;
    color: #333333;
    font-size: 16px;
    font-family: sans-serif;
  }
  .fan_num{
    height: 20px;
    line-height: 20px;
    color: #333333;
    font-size: 16px;
    font-family: sans-serif;
  }
</style>



