<template>
  <div class="detail_product_container">
    <div class="list_ul">
        <li class="list_item" v-for="(item,index) in datas.goods_image" >
          <img :src="item" class="good_img">
        </li>
      <div class="middle_part">
        <span class="left_line"></span>
        <span class="smailier_good">类似商品</span>
        <span class="right_line"></span>
      </div>
    </div>
    <div class="grid_container">
      <ul class="clearfix" >
        <li v-for="(item,index) in datas.salesGoods" class="item" @click="goodDetail(datas.salesGoods[index].goods_id)">
          <img  class="good_img" v-lazy="item.goods_image">
          <!--<img src="" class="good_img">-->
          <div class="desc">
            <p class="good_name">{{item.goods_name }}</p>
            <div class="num_price">
              <p class="good_price">￥{{item.goods_price }}</p>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  import router from '../../router/index'
  import store from '../../store/store'
  export default {
    data(){
      return{
        goodId:''
      }
    },
      props:{
        datas:{
          type: Object
        }
      },
    methods:{
      //        商品详情
      goodDetail(goodId){
        const path='/gooddetail/'+goodId;
        router.push({ path: path })
        this.goodId=goodId;
      },
    },
    watch: {
      '$route' (to, from) {
        // 对路由变化作出响应...
        this.$emit("refreshEvent",this.goodId)
      }
    }

  }
</script>
<style scoped>
  @import "../../css/reset.css";
  @import "right_list.css";
</style>



