import { Toast } from 'mint-ui';
const util={
  //验证手机号格式
     vaildPhone:function (phone) {
       if(phone.length==0){
         this.ToastUtil('请输入手机号码');
         return false;
       }
       let myreg =  /^1\d{10}$/ ;
       if(phone.length!=11) {
         this.ToastUtil('请输入有效的手机号码！')
         return false;
       }
       if(!myreg.test(phone)){
         this.ToastUtil('请输入有效的手机号码！')
         return false;
       }
         return true
     },
  ToastUtil(msg){
    Toast({
      message: msg,
      position: 'bottom',
      duration: 3000
    });
  }
}
export default util;



