<template lang="html">
  <div class="footer_nav" v-if="showFooter">

    <ul>
      <li @click="switchTab(1)">
        <div :class="($store.state.currentTab===1)? 'nav_home_active' : 'nav_home_normal'" ></div>
        <p :class="($store.state.currentTab===1)? 'text_indicator' : 'text_indicator_active'">首页</p>
      </li>
      <li @click="switchTab(2)">
        <div :class="($store.state.currentTab===2)? 'nav_find_active' : 'nav_find_normal'" ></div>
        <p :class="($store.state.currentTab===2)? 'text_indicator' : 'text_indicator_active'">店铺</p>
      </li>
      <li @click="switchTab(3)">
        <div  :class="($store.state.currentTab===3)? 'nav_order_active' : 'nav_order_normal'" ></div>
        <p :class="($store.state.currentTab===3)? 'text_indicator' : 'text_indicator_active'">分类</p>
      </li>
      <li @click="switchTab(4)">
        <div :class="($store.state.currentTab===4)? 'nav_car_active' : 'nav_car_normal'" ></div>
        <p :class="($store.state.currentTab===4)? 'text_indicator' : 'text_indicator_active'">购物车</p>
      </li>
      <li @click="switchTab(5)">
        <div :class="($store.state.currentTab===5)? 'nav_mine_active' : 'nav_mine_normal'" ></div>
        <p :class="($store.state.currentTab===5)? 'text_indicator' : 'text_indicator_active'">我的</p>
      </li>
    </ul>
  </div>
</template>
<script>
  import store from '../store/store'
  import router from '../router/index'
  export default {
    props:{
      showFooter:{
        type:Boolean,
        default:true
      }
    },
    data(){
      return{

      }
    },
    methods:{
      switchTab(index){
        switch (index){
          case 1:
//             修改vuex中currentPage的值为1,默认为1
            this.$store.commit("switchTab",1);
            router.push({ path: '/index' })
            break;
          case 2:
            //             修改vuex中currentPage的值为2
            this.$store.commit("switchTab",2);

            router.push({ path: '/shop' })

            break;
          case 3:
            //             修改vuex中currentPage的值为3
            this.$store.commit("switchTab",3);
            router.push({ path: '/category' })
            break;
          case 4:
            //             修改vuex中currentPage的值为4
            this.$store.commit("switchTab",4);
            router.push({ path: '/shopCar' })
            break;
          case 5:
            this.judgeLogon();

            break;
        }

      },
//      判断是否登录
      judgeLogon(){
        let token =localStorage.getItem('token');
         console.log("=====islogin======="+token)
        if(!token){
          router.push({ path: '/login' })
        }else{
          // 修改vuex中currentPage的值为4
          this.$store.commit("switchTab",5);
          router.push({ path: '/mine' })
        }
      }
    },
    created(){


      console.log(this.showFooter);
    }
  }
</script>
<style lang="css" scoped>
 @import "../css/reset.css";
 .footer_nav{
   width: 100%;
   height: 54px;
   position: fixed;
   left: 0;
   bottom: 0;
   border-top: 0.2rem solid #ccc;
   background-color: white;
   padding: 0.3rem 0;
 }
 .footer_nav ul{
   display: flex;
   height: 100%;
   align-items: center;
 }
 .footer_nav ul li{
   display: flex;
   width: 25%;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   background-color: white;
 }
 .text_indicator{
   font-size: 16px;
   margin-top: 0.4rem;

   color: #0BA062;
 }
 .text_indicator_active{
   font-size: 16px;
   margin-top: 0.4rem;
   color: #2c2c2c;
 }
 .nav_home_normal{
   width: 25px;
   height: 25px;
   background-image: url('../assets/images/menu_home_unselect.png');
   background-repeat: no-repeat;
   background-size: 25px 25px;
 }
 .nav_home_active{
   width: 25px;
   height: 25px;
   background-image: url('../assets/images/menu_home_selected.png');
   background-repeat: no-repeat;
   background-size: 25px 25px;
 }
 .nav_find_normal{
   width: 30px;
   height: 30px;
   background-image: url('../assets/images/menu_shop_unselect.png');
   background-repeat: no-repeat;
   background-size: 30px 30px;
 }
 .nav_find_active{
   width: 30px;
   height: 30px;
   background-image: url('../assets/images/menu_shop_selected.png');
   background-repeat: no-repeat;
   background-size: 30px 30px;
 }
 .nav_order_normal{
   width: 25px;
   height: 25px;
   background-image: url('../assets/images/menu_category_unselect.png');
   background-repeat: no-repeat;
   background-size: 25px 25px;
 }
 .nav_order_active{
   width: 25px;
   height: 25px;
   background-image: url('../assets/images/menu_category_selected.png');
   background-repeat: no-repeat;
   background-size: 25px 25px;
 }
 .nav_car_normal{
   width: 28px;
   height: 28px;
   background-image: url('../assets/images/menu_shopcar_unselect.png');
   background-repeat: no-repeat;
   background-size: 28px 28px;
 }
 .nav_car_active{
   width: 28px;
   height: 28px;
   background-image: url('../assets/images/menu_shopcar_selected.png');
   background-repeat: no-repeat;
   background-size: 28px 28px;
 }

 .nav_mine_normal{
   width: 28px;
   height: 28px;
   background-image: url('../assets/images/menu_mine_unselect.png');
   background-repeat: no-repeat;
   background-size: 28px 28px;
 }
 .nav_mine_active{
   width: 28px;
   height: 28px;
   background-image: url('../assets/images/menu_mine_selected.png');
   background-repeat: no-repeat;
   background-size: 28px 28px;
 }
</style>



