       <template lang="html">
         <div class="detail_container">
           <div>
          <div class="top_header">
            <header-back :titles="title" @back="back"></header-back>
          </div>
            <div class="content">
              <!--banner-->
              <div class="banner">
                <mt-swipe :auto="4000" @change="handleChange">
                  <mt-swipe-item  v-for="item in datas.goods_image_header">
                    <img :src="item" alt="" class="banner_img" >
                  </mt-swipe-item>
                </mt-swipe>
              </div>
              <!--商品参数-->
              <div class="good_param">
                <p class="good_name">{{datas.goods_name}}</p>
                <p class="good_price">￥&nbsp;{{datas.goods_price}}</p>
                <p class="return_cash">返现￥{{backCash }}元</p>
                <div class="origin_address_storge">
                  <p class="address" v-if="datas.storeInfo.area_info">发货地 :&nbsp;{{datas.storeInfo.area_info}}</p>
                  <p class="storges">库存 {{datas.goods_storage}}件</p>
                </div>
              </div>
              <!--导航栏切换-->
              <good-detail-switch @switchIndex="switchIndex" ></good-detail-switch>
              <!--左侧导航栏-->
              <detail-list v-if="flag" :datas="datas" @refreshEvent="refresh"></detail-list>
              <!--右侧导航栏-->
              <product-detail v-show="!flag" :datas="datas" @refreshEvent="refresh"></product-detail>
            </div>
             <!--footer-->
            <div class="footer_container">
              <add-buy @addshopCar="addshopCar" @buy="buy"></add-buy>
            </div>
           </div>
           <div class="mask1" v-if="showCar" >
             <!--购物车dialog--> <!--购买dialog-->
             <div class="shopcarlist" v-if="showCar">
               <div class="gc_info">
                 <img :src="datas.goods_image_header[0]" class="good_image">
                 <div class="price_storge">
                   <p class="price">￥ {{datas.goods_price}}</p>
                   <p class="storages">库存 {{datas.goods_storage }}</p>
                   <p class="select_size_color">选择颜色、尺寸</p>
                 </div>
                 <span class="close" @click="closeAddDialog"></span>
               </div>
               <div class="good_size">
                 <p class="p_size">尺寸</p>
                 <ul class="size_list">
                   <li class="size_item">jksjf</li>
                   <li class="size_item">sdfsf</li>
                   <li class="size_item">sfsf</li>
                 </ul>
               </div>
               <div class="good_color">
                 <p class="p_color">颜色</p>
                 <ul class="color_list">
                   <li class="color_item">sdfsdf</li>
                   <li class="color_item">sdfsdf</li>
                   <li class="color_item">sdfsdf</li>
                 </ul>
               </div>
               <!--购买数量-->
               <div class="buy_num">
                 <span>购买数量</span>
                 <div class="edit_num">
                   <edit-num :storge="datas.goods_storage" ></edit-num>
                 </div>
               </div>
               <div class="confirm_add grad" @click="ensure_add">
                 <span >确定</span>
               </div>
             </div>
           </div>


         </div>
       </template>
       <script>
         import HeaderBack from '../../components/header_back'
         import AddBuy from './buy_add'
         import GoodDetailSwitch from './good-detail-switch'
         import ProductDetail from './product_detail_list'
         import DetailList from './detal_goodlist.vue'
         import EditNum from '../../components/edit-num'
         import ToastUtils from '../../common/utils'
         import Vue from "vue"
         import qs from 'qs';
         export default {
           data(){
             return{
               title:'商品详情',
               flag:true,
               datas:{},
               showCar:false,
               showBuy:false,
               addNum:0,
               //1.表示加入购物车 2.表示立即购买
               from:1,
           }},
           computed:{
             backCash(){
               return this.datas.goods_return_rate*this.datas.goods_price;
             }
           },
           components:{
             HeaderBack,
             AddBuy,
             GoodDetailSwitch,
             ProductDetail,
             DetailList,
             EditNum
           },
           watch:{
             showCar(val, oldVal){
               if(val){
                 document.getElementsByTagName("body")[0].className="active_pop";
               }else{
                 document.body.removeAttribute("class","active_pop");
               }
             }
           },
           methods:{

             //确认加入购物车
             ensure_add(){
              let carNum=this.$store.state.addCarNum;
               if(carNum>0){
                 //加入购物车成功
                 if(this.from===1){
                   this.addShopSucess(this.$route.params.id,carNum);
                 }else{ //立即购买
                   this.instantBuy(this.$route.params.id,this.$store.state.addCarNum);
                 }
               }else{ //《=0
                 ToastUtils.ToastUtil("请选择数量");
               }
             },

             //立即购买
             instantBuy(goodId,num){

               console.log("====goodId==="+goodId+"====num===="+num);
               let token=localStorage.getItem('token');
//               var data = qs.stringify({
//                 cart_id: goodId+"|"+num,
//                 ifcart: '0'
//               });
//               this.$http.post('gp/mobile/index.php?' + 'act=member_buy&op=buy_step1&token='+token+"&method=post", data,{
//                 headers: {
//                   'Content-Type': 'application/x-www-form-urlencoded'
//                 }
//               })
//                 .then(function (response) {
//                   console.log(response);
//                 })
//                 .catch(function (response) {
//                   console.log(response);
//                 });

               //vue-resource
               this.$http.post('gp/mobile/index.php?' + 'act=member_buy&op=buy_step1&token='+token+"&method=post",
                 {
                   'cart_id': goodId+"|"+num,
                   'ifcart': '0',
                 },
                 {emulateJSON:true}).then(
                 function (res) {
                   // 处理成功的结果
                   console.log(res.body)

                 },function (err) {
                   // 处理失败的结果
                   console.log(err)
                 });
             },

             //加入购物车
             addShopSucess(goodId,carNum){
               let token=localStorage.getItem('token');
               let that=this;
//               this.$http.jsonp('http://gbfmall.slh8.com/mobile/index.php?act=member_cart&op=cart_add&token='+token,
               this.$http.jsonp('gp/mobile/index.php?act=member_cart&op=cart_add&token='+token,
                 {
                   params: {
                     goods_id:goodId,
                     quantity:carNum
                   },
                   jsonp:'callback',
                   jsonpcallback	:'addtoCart',
                 }).then(function(res){
                 const jsonStr=JSON.parse(res.bodyText);

                 if(jsonStr.code===0){
                  ToastUtils.ToastUtil(jsonStr.msg);
                  that.showCar=false;
                 }

               },function(){
                 Toast("加入购物车请求失败！！！")
               });
             },
             closeAddDialog(){
               this.showCar=false;
             },
             refresh(goodId){
               this.initDetail(goodId)
             },
             //banner点击事件
             handleChange(index) {
              console.log('item   '+index)
             },
             back(){
               this.$router.go(-1);
             },
             //加入购物车
             addshopCar(){

               this.showCar=!this.showCar;
               this.from=1;
             },
             //立即购买
             buy(){

               this.showCar=!this.showCar;
               console.log("立即购买")
               this.from=2;
             },
             switchIndex(index){
               if(index===1){//产品参数
                  this.flag=true
               }else{//商品详情
                 this.flag=false
               }
             },
             initDetail(goodId){
                     let token=localStorage.getItem('token');
               let that=this;
               this.$http.jsonp('http://gbfmall.slh8.com/mobile/index.php?act=goods&op=goodsDetail',
                 {
                   params: {
                     goods_id:goodId,
                     token:token
                   },
                   jsonp:'callback',
                   jsonpcallback	:'detail',
                 }).then(function(res){
                  that.datas=JSON.parse(res.bodyText).datas;
               },function(){
                 Toast("商品详情数据请求失败！！！")
               });
             }
           },
           created(){
             this.initDetail(this.$route.params.id);
             console.log("======goodId===="+this.$route.params.id)
           }
         }
       </script>
       <style lang="css" scoped >
         @import "../../css/reset.css";
         @import "detail.css";
       </style>



