<template>
  <div class="shop_container">
    <div class="top_nav">
    <header-view :titles="title"></header-view>
    </div>
    <div class="shop_list">
      <ul>
        <li class="shop_item" v-for="item in shopList">
          <div class="one">
            <div class="left">
              <img :src="member_avatar_path+item.member_avatar" class="shop_logo">
              <p class="shop_name">{{item.store_name}}</p>
            </div>
            <div class="right">
             <div class="collect"></div>
             <div class="share"></div>
            </div>

          </div>
          <div class="two">
            <img :src="store_banner_path+item.store_banner " class="shop_cover">
            <p class="desc">粉丝：{{item.store_collect}}人,商品数{{item.goods_count }}件</p>
          </div>
        </li>

      </ul>
    </div>
  </div>
</template>
<script>
  import HeaderView from '../../components/header-view'
  export default {
       data(){
         return{
           title:'店铺',
           shopList:[],
           member_avatar_path:'',
           store_banner_path:''
         }
       },
       components:{
         HeaderView
       },
    methods:{
         //获取店铺列表
      getShopInfo(){
        let token='';
        let that=this;
        this.$http.jsonp('http://gbfmall.slh8.com/mobile/index.php?act=store&op=storeList&token='+token,
          {
            jsonp:'callback',
            jsonpcallback	:'shopInfo',
          }).then(function(res){
          console.log('====shopInfo========'+JSON.parse(res.bodyText).datas)
          that.shopList=JSON.parse(res.bodyText).datas.store_list;
          that.member_avatar_path=JSON.parse(res.bodyText).datas.member_avatar_path;
          that.store_banner_path=JSON.parse(res.bodyText).datas.store_banner_path;

        },function(){
          console.log("shopInfo请求失败！！！")
        });
      }
    },
    created(){
      //显示底部导航栏
      this.$store.commit("showFooterTab",true);
      this.getShopInfo();
    }
  }
</script>
<style scoped lang="css">
  @import "../../css/reset.css";
  .top_nav{
    position: fixed;
    height: 4.3rem;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 3;
  }
  .shop_list{
    width:100%;
    margin-top: 4.8rem;
    margin-bottom: 65px;
    background-color: #fff;
    box-sizing: border-box;
    padding: 0 1rem;
  }
  .shop_item{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 280px;
    margin-bottom: 0.5rem;
  }
  .one{
    display: flex;
    width: 100%;
    height: 6rem;
    justify-content: space-between;
    align-items: center;
  }
  .left{
    display: flex;
    margin-left: 1.5rem;
    align-items: center;
  }
  .shop_logo{
    display: block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
  .shop_name{
    font-family: "SimSun", "宋体";
    font-size: 16px;
    color: #333333;
    padding-left: 1rem;
  }
  .right{
    display: flex;
    margin-right: 1.5rem;
    align-items: center;
  }
  .collect{
    width: 24px;
    height: 24px;
    background-image: url("../../assets/images/collect_unslect.png");
    background-repeat: no-repeat;
    background-size: 24px 24px;
  }
  .share{
    width: 24px;
    height: 24px;
    margin-left: 1.5rem;
    background-image: url("../../assets/images/share (1).png");
    background-repeat: no-repeat;
    background-size: 24px 24px;
  }
  .two{

    width: 100%;
    height: 200px;
    margin-top: 1rem;
    position: relative;
  }
  .shop_cover{
    width: 100%;
    height: 100%;
  }
  .two img[lazy=loading]{
    width: 4rem;
    margin: auto;
  }
  .desc{
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 100%;
    height: 30px;
    line-height: 30px;
    background-color:#000000;
    opacity:0.5;
    color: white;
    font-size: 14px;
    box-sizing: border-box;
    padding-left: 1.5rem;
    font-family: "SimSun", "宋体";
  }

</style>



