<template>
  <div class="mine_container">
    <div class="top_header">
    <header-view :titles="title"></header-view>
    </div>
    <div class="user_info">
      <img :src="datas.member_avatar" alt="" class="user_avator">
      <p class="user_name">{{datas.member_nickname}}</p>
      <div class="member_info">
        <p class="member_num">会员编号：{{datas.member_id}}</p>
      </div>
    </div>
    <div class="one">
        <div class="remain_moneny">
          <p>￥{{datas.civilian_available_predeposit}}元</p>
          <p>账户余额</p>
        </div>
      <div class="reward_money">
        <p>￥{{datas.return_cache}}</p>
        <p>奖励金</p>
      </div>
      <div class="bank_num">
        <p>{{datas.member_credit}}</p>
        <p>银行卡</p>
      </div>
    </div>

    <div class="two">
      <div class=" item invite">
        <img src="../../assets/images/invite.png" class="item_icon">
        <p class="item_name">邀请</p>
        <div class="go_right"></div>
      </div>
      <div class=" item order">
        <img src="../../assets/images/order.png" class="item_icon">
        <p class="item_name">订单</p>
        <div class="go_right"></div>
      </div>

      <div class=" item nopay">
        <img src="../../assets/images/nopay.png" class="item_icon">
        <p class="item_name">未支付</p>
        <div class="go_right"></div>
      </div>
      <div class=" item nosend_good">
        <img src="../../assets/images/no_send_good.png" class="item_icon">
        <p class="item_name">待发货</p>
        <div class="go_right"></div>
      </div>
      <div class=" item noReceive_good">
        <img src="../../assets/images/no_receive.png" class="item_icon">
        <p class="item_name">待收货</p>
        <div class="go_right"></div>
      </div>

      <div class=" item apply_open_shop">
        <img src="../../assets/images/apply_open_shop.png" class="item_icon">
        <p class="item_name">申请开店</p>
        <div class="go_right"></div>
      </div>
      <div class=" item sell_after_center">
        <img src="../../assets/images/seller_center.png" class="item_icon">
        <p class="item_name">售后中心</p>
        <div class="go_right"></div>
      </div>
      <div class=" item custom_questions">
        <img src="../../assets/images/custom_question.png" class="item_icon">
        <p class="item_name">常见问题</p>
        <div class="go_right"></div>
      </div>

    </div>
  </div>
</template>
<script>
  import HeaderView from '../../components/header-view'
  export default {
      data(){
        return{
          title:'我的',
          datas:{}
        }
      },
    methods:{
        //获取用户信息
        getMemberInfo(){
          let token=localStorage.getItem('token');
          let that=this;
          this.$http.jsonp('http://gbfmall.slh8.com/mobile/index.php?act=member&op=edit&token='+token,
            {
              jsonp:'callback',
              jsonpcallback	:'forgetpass',
            }).then(function(res){
             that.datas=JSON.parse(res.bodyText).datas;
            console.log("=====用户中心===code======"+JSON.parse(res.bodyText))
          },function(){
            console.log("用户中心请求失败！！！")
          });
        }
    },
    components:{
        HeaderView
    },
    created(){
      //显示底部导航栏
      this.$store.commit("showFooterTab",true);
    },
    mounted(){
      this.getMemberInfo();
    }
  }
</script>
<style scoped lang="css">
@import "../../css/reset.css";
.mine_container{
  background-color: #f9f9f9;
}
  .top_header{
    width: 100%;
    position: fixed;
    top:0;
    left:0;
    z-index: 3;
  }
  .user_info{
    position: relative;
    display: flex;
    flex-direction: column;
    margin-top: 58px;
    width: 100%;
    height: 140px;
    background-color: #ffffff;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
  }
  .user_avator{
    display: block;
    width: 60px;
    height: 60px;
    background-color: #0BA062;
    border-radius: 50%;
  }
  .user_name{
    padding-top: 10px;
    font-family: sans-serif;
    font-size: 16px;
    color: #141414;
  }
  .member_info{
    display: flex;
    width: 100%;
    margin-bottom: 10px;
  }

  .member_num{
    flex: 1;
    text-align: center;
    color: #141414;
    font-size: 13px;
  }
  .one{
    box-sizing: border-box;
    display: flex;
    width: 100%;
    height: 50px;
    background-color: white;
    align-items: center;
    margin-bottom: 1rem;
    border-top: 0.1rem solid #f9f9f9;
  }
  .remain_moneny{
    display: flex;
    flex-direction: column;
    flex: 1;
    height: 100%;
    justify-content: center;
    align-items: center;
  }
  .remain_moneny p{
   padding: 0.2rem 0;
    font-size: 2.6rem;
  }
  .reward_money{
    display: flex;
    flex-direction: column;
    flex: 1;
    height: 100%;
    justify-content: center;
    align-items: center;
  }
.reward_money p{
  font-size: 2.6rem;
  padding: 0.2rem 0;
}
  .bank_num{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1;
    height: 100%;
  }
  .bank_num p{
    font-size: 2.6rem;
    padding: 0.2rem 0;
  }
  .two{
    margin-bottom: 7rem;
  }
.item{
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 42px;
  background-color: #fff;
  border-bottom: 0.1rem solid #f9f9f9;
  align-items: center;
}
  .item_icon{
     display: block;
     width: 23px;
     height: 23px;
     margin-left: 2rem;
  }
  .item_name{
    margin-left: 1.5rem;
    flex:1;
    font-size: 2.6rem;
  }
  .go_right{
    width: 23px;
    height: 23px;
    background-image: url("../../assets/images/go_right.png");
    background-repeat: no-repeat;
    background-size:23px 23px;
    margin-right: 1.5rem;
  }
</style>



