<template lang="html">
  <div class="edit_container">
    <span class="add"  v-tap="{methods:add}">+</span>
    <span class="text_value">{{num}}</span>
    <span class="sub"  v-tap="{methods:sub}">-</span>

  </div>
</template>
<script>
  import ToastUtils from '../common/utils'
  export default {
    props:{
      storge:{
        type:""
      }
    },
      data(){return{
        num:0
      }},
    created(){
        if(this.storge>=1){
          this.num=1;
          this.$store.commit("addShopCarNum",this.num)
        }
        console.log("======商品个数====="+this.num)
//      this.$emit("addNum",this.num)

    },
    methods:{
      add(){
        if(this.num>=this.storge){
           ToastUtils.ToastUtil("库存不足")
        }else{
          this.num++;
//          this.$emit("addNum",this.num)
          this.$store.commit("addShopCarNum",this.num)

        }

      },
      sub(){
        if(this.num>0){
          this.num--;
//          this.$emit("addNum",this.num)
          this.$store.commit("addShopCarNum",this.num)
        }else{
          this.num=0
//          this.$emit("addNum",this.num)
          this.$store.commit("addShopCarNum",this.num)
        }
      }
    }
  }
</script>
<style lang="css" scoped>
@import "../css/reset.css";
  .edit_container{
    display: flex;
    width: 160px;
    height: 32px;
    background-color: purple;
    align-items: center;
    justify-content: center;
  }
  .add{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    flex: 1;
    background-color: #F5F5F5;
    border: 0.2px solid #ffffff;
    font-size: 16px;
  }
.text_value{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  flex: 1;
  border: 0.2px solid #F5F5F5;
  background-color: #F5F5F5;
  font-size: 16px;
}
.sub{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  flex: 1;
  background-color: #F5F5F5;
  border: 0.2px solid #F5F5F5;
  font-size: 16px;
}
</style>



