<!--注册页面-->
<template lang="html">
  <div class="regist_container">
    <header-view :titles="title" @back="back"></header-view>
    <div class="phone">
      <span class="phone_icon"></span>
      <div class="phone_input">
        <input type="text" style="display: none;"/>

        <input type="number" placeholder="请输入手机号" v-model.trim="mobile" debounce="500" autocomplete="new-password">
      </div>
    </div>
    <div class="password">
      <span class="pwd_icon"></span>
      <div class="password_input">
        <input type="password" style="display: none;"/>

        <input type="password" placeholder="请输入密码" v-model.trim="pwd"  autocomplete="new-password">
      </div>
    </div>

    <div class="verfycode">
      <span class="verfycode_icon"></span>
      <div class="verfycode_input">
        <input type="text" style="display: none;"/>
        <input type="number" placeholder="请输入验证码" v-model.trim="identify_code"  autocomplete="new-password">
      </div>
      <div class="line"></div>
      <div class="send_verfy_code" @click="sendCode">
        <span>发送验证码</span>
      </div>
    </div>
    <div class="regist_btn" @click="regist">注册</div>
  </div>
</template>
<script>
  import HeaderView from '../../components/header_back'
  import Utils from '../../common/utils'
  export default {
      data(){
        return{
         title:"注册",
          mobile:'',
          pwd:'',
          identify_code:'',
          hasRegist:false //是否注册过
      }
      },
    watch:{
        //通过观察手机号，来检查该手机号是否被注册过
      mobile:{
        handler:function (val ,oldval) {
          if(val.length===11){
            this.checkMobile();
          }
        }
      },
      deep:true
    },
      components:{
        HeaderView
      },
    methods:{
        back(){
          this.$router.go(-1);
        },
      //检查账号是否注册
      checkMobile(){
        if(!Utils.vaildPhone(this.mobile)){
          return;
          }
        this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=login&op=checkMobile", //跨域请求接口
          {
            params: {
              username:this.mobile
            },
            jsonp:'callback',
            jsonpcallback	:'checkUserName',
          }).then(function(res){
          const response=JSON.parse(res.bodyText);
          if(response.code===5){
            Utils.ToastUtil(response.msg);
            this.hasRegist=true
          }else{
            this.hasRegist=false
          }
        },function(){
          Utils.ToastUtil("请求失败！！！")
        });
      },
      //发送验证码
      sendCode(){
        if(this.hasRegist){
          Utils.ToastUtil("手机号已经被注册过了");
          return;
        }
        this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=sms&op=index", //跨域请求接口
          {
            params: {
              mobile:this.mobile
            },
            jsonp:'callback',
            jsonpcallback	:'verifyCode',
          }).then(function(res){
          const response=JSON.parse(res.bodyText);
          Utils.ToastUtil(response.msg)
        },function(){
          Utils.ToastUtil("请求失败！！！")
        });

      },
      //注册
      regist(){
        if(this.hasRegist){
          Utils.ToastUtil("手机号已经被注册过了");
          return;
        }
        if(!Utils.vaildPhone(this.mobile)){
          return;
        }
        if(this.pwd.length===0){
          Utils.ToastUtil("请输入手机号");
          return;
        }
        this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=login&op=register", //跨域请求接口
          {
            params: {
              member_name : this.mobile,
              member_password : this.pwd,
              invite_code :"" ,
              sms_code : this.identify_code
            },
            jsonp:'callback',
            jsonpcallback	:'register',
          }).then(function(res){
          const response=JSON.parse(res.bodyText);
          localStorage.setItem('token',response.datas.token.token)
        },function(){
          Utils.ToastUtil("请求失败！！！")
        });

      }
    }

  }
</script>
<style scoped lang="css">
@import "../../css/reset.css";
  @import "regist.css";
</style>



