<!--带返回键的header-->
<template lang="html">
  <div class="top_header">
    <p>{{titles}}</p>
    <span class="back_icon" @click="back"></span>
  </div>
</template>
<script>
  export default {
    props:{
         titles:{
           type:''
         }
    },
    methods:{
      //返回
      back(){
        this.$emit("back")
      }
    }

  }
</script>
<style lang="css" scoped>
  @import '../css/reset.css';
  .top_header{
    position: relative;
    display:flex;
    width: 100%;
    height: 50px;
    background-color: #F5F5F5;
    align-items: center;
    justify-content: center;
  }
  .top_header p{
    color: #141414;
    font-size: 16px;
    font-family: "Helvetica Neue", Helvetica, Arial,
    "PingFang SC", "Hiragino Sans GB",
    "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;

  }
  .back_icon{
    position: absolute;
    width: 24px;
    height: 24px;
    top: 12px;
    left: 1rem;
    background-image: url("../assets/images/back_left.png");
    background-repeat: no-repeat;
    background-size: 24px 24px;
  }
</style>



