<template lang="html">
<div class="search_container">

  <div class="top_container">
     <div class="back_icon" @click="back"></div>
    <div class="search_wrapper">
      <input type="search" class="input_content" placeholder="请输入搜索的商品和店铺">
    </div>
    <p class="search_indicator" @click="searchContent">搜索</p>
  </div>
  <div class="hot_search">
       <ul class="ul_label clearfix">
         <li class="hot_item" v-for="(item ,index) in hotWord">{{item}}</li>
       </ul>
  </div>

</div>
</template>
<script>
  export default {
    data(){
      return{
        hotWord:[]
      }
    },
      methods:{
        // 返回上一级
        back(){
          this.$router.go(-1);
        },
//        获取热门关键词
        getHotWord(){
          var that=this;
          this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=index&op=search",
            {
              jsonp:'callback',
              jsonpcallback	:'hotword',
            }).then(function(res){
              console.log('========热词===='+JSON.parse(res.bodyText))
            that.hotWord=JSON.parse(res.bodyText).datas.hotWord;
          },function(){
            console.log("热词数据请求失败！！！")
          });
        },
        // 点击搜索
        searchContent(){

        }
      },
    created(){
        this.getHotWord();
    }
  }
</script>
<style lang="css" scoped>
  @import "../../css/reset.css";
  .search_container{
    width: 100%;
    background-color: #fff;
  }
  .top_container{
    position: fixed;
    top:0;
    left: 0;
    z-index: 3;
    display: flex;
    width: 100%;
    height: 4rem;
    background-color: #0BA062;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
  }
  .back_icon{
    width: 2.7rem;
    height: 2.7rem;
    margin-left: 1.6rem;
    background-image: url("../../assets/images/back_icon.png");
    background-repeat: no-repeat;
    background-size: 1.7rem 2.4rem;
  }
  .search_wrapper{
    width: 60%;
    height: 2.9rem;

  }
  .input_content{
    width: 100%;
    height: 100%;
    background-color: #fff;
    border: 0.1rem solid transparent;
    border-radius: 0.5rem;
    padding: 0 1rem;
  }
  .search_indicator{
    padding: 0.5rem;
    margin-right: 1.6rem;
    color: white;
    font-size: 2.4rem;
    font-family: "SimSun", "宋体";
    font-weight: 700;
  }
  .hot_search{
    margin-top: 10rem;
    width: 100%;
  }
  .ul_label{
    width: 100%;
    box-sizing: border-box;
    padding: 2rem 1.5rem;
  }
  .hot_item{
    float: left;
    width: 30%;
    height: 3.6rem;
    text-align: center;
    line-height: 3.6rem;
    border: 0.1rem solid transparent;
    border-radius: 15rem;
    background-color: #0BA062;
    color: white;
    box-sizing: border-box;
    margin-right: 0.3rem;
  }
  .clearfix:after{
    display: block;
    content: '.';
    height: 0;
    clear: both;
    visibility: hidden;
  }
</style>



