<!--加入购物车和立即购买-->
<template lang="html">
   <div class="footer_nav">
     <div class="add" @click="add">加入购物车</div>
     <div class="buy" @click="buy">立即购买</div>
   </div>
</template>
<script>
  export default {
       data(){
         return{}
       },
    methods:{
         add(){
           this.$emit('addshopCar')
         },
      buy(){
          this.$emit("buy")
      }
    }
  }
</script>
<style lang="css" scoped>
  @import '../regist/regist.css';
  .footer_nav{
    display: flex;
    width: 100%;
    height: 54px;
    background-color: skyblue;
  }
  .buy{
    display: flex;
    flex: 1;
    height: 100%;
    background-color: #0ba062;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 2.6rem;
  }
  .add{
    display: flex;
    flex: 1;
    height: 100%;
    background-color: red;
    justify-content: center;
    align-items: center;
    font-size: 2.6rem;
    color: #141414;

  }
</style>



