<template lang="html">
  <div class="grid_container">

      <ul class="clearfix" v-infinite-scroll="loadMore"
          infinite-scroll-disabled="loading"
          infinite-scroll-distance="10" >
        <li v-for="(item,index) in goodlist" class="item" @click="goodDetail(goodlist[index].goods_id)">
          <img  class="good_img" v-lazy="item.goods_image">
          <!--<img src="" class="good_img">-->
          <div class="desc">
            <p class="good_name">{{item.goods_name }}</p>
            <div class="num_price">
              <p class="num">库存 {{item.goods_storage}} 件</p>
              <p class="good_price">￥{{item.goods_price}}</p>
            </div>
            <div class="label_fan">
              <p class="member_fan">会员返现{{item.goods_return_rate }}元</p>
            </div>

          </div>
        </li>
      </ul>
  </div>
</template>
<script>
  export default {
      props:{
        goodlist:{
          type:Array
        }
      },
    data(){
        return{
          loading: false,
          allLoaded: false,
          wrapperHeight: 0,
          page:1
        }
    },
   computed:{
     returnCash(item){
       return item.goods_price*item.goods_return_rate*0.2;
     }
   },
    methods:{
      loadMore() {
        this.page++;
        console.log("========上拉===111=  this.data.currentpage="+this.page)
        this.$emit('upload',this.page);
      },
      goodDetail(goodId){
        this.$emit('seeDetail',goodId)
      }
    }
  }
</script>
<style lang="css" scoped>
  @import "../css/reset.css";
  .grid_container{
    width: 100%;
    padding-bottom: 65px;
  }
  .grid_container ul{
    width: 100%;
    background-color: #f9f9f9;
  }

  .item{
    float: left;
    width: 50%;

    box-sizing: border-box;
    background-color: #fff;
    border-bottom: 0.5rem solid #f9f9f9;
  }
  /*.item img[lazy=loading]{*/
    /*width: 4rem;*/
    /*margin: auto;*/
  /*}*/
  .good_img {
    display: block;
    width: 100%;

  }

  .item:nth-of-type(odd){
    border-right: 0.5rem solid #f9f9f9;
  }

  .item:nth-of-type(even){
    border-left: 0.5rem solid #f9f9f9;
  }

  .page-infinite-loading{
    text-align: center;
    height: 5rem;
    line-height: 5rem;
  }

  .desc{
    width: 100%;
    box-sizing: border-box;
    padding: 1rem 1rem;
  }
  .good_name{
    display: block;
    width:100%;
    height: 20px;
    line-height: 20px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #333333;
    font-family: sans-serif;
    font-size: 2.5rem;
    padding: 0 0.3rem;
    box-sizing: border-box;
  }
  .num_price{
    display:flex;
    width: 100%;
    margin-top: 0.5rem;
    height: 20px;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 0.3rem;
    padding-right: 0.5rem;
  }
  .num{
    color: #333333;
    font-family: sans-serif;
    font-size: 2.2rem;
    padding-left: 0.5rem;
  }
  .good_price{
    color: #333333;
    font-family: sans-serif;
    font-size:2.2rem;
  }
  .member_fan{
    margin-top: 10px;
    display: table-cell;
    font-size: 10px;
    display: table-cell;
    vertical-align: middle;
    border: 0.1rem solid #0BA062;
    box-sizing: border-box;
    padding: 0.3rem 0.5rem;
    border-radius: 0.8rem;
  }
  .clearfix:after{
    display: block;
    content: '.';
    height: 0;
    clear: both;
    visibility: hidden;
  }
  .label_fan{
    display: table;
  }
</style>



