<template>
  <div class="nav_container">
    <div class="item "
         :class="(currentIndex===1)? 'supermarket_active' : 'supermarket'"
         @click="selectIndex(1)">进口超市批发</div>
    <div class="item "
         :class="(currentIndex===2)? 'luxury_good_active' : 'luxury_good'"
         @click="selectIndex(2)">奢侈品</div>
    <div class="item "
         :class="(currentIndex===3)? 'europe_art_good_active' : 'europe_art_good'"
         @click="selectIndex(3)">欧洲艺术品</div>
  </div>
</template>
<script>
  export default {
    data(){
      return{
        currentIndex:1
      }
    },
    methods:{
      selectIndex(index){
         this.currentIndex=index;
         this.$emit("selectTab",this.currentIndex);
      }
    }
  }
</script>
<style lang="css" scoped>
  @import '../css/reset.css';
  .nav_container{
    display: flex;
    width: 100%;
    height: 46px;
    background-color: white;
    justify-content: space-between;
    align-items: center;
  }
  .item{
    font-family: "SimSun", "宋体";
    font-size: 2.6rem;
    box-sizing: border-box;
    padding: 1rem 0;
  }
  .supermarket{
    margin-left: 1.5rem;
    border-bottom: 0.2rem solid transparent;
    color: #333333;
  }
  .supermarket_active{
    margin-left: 1.5rem;
    border-bottom: 0.2rem solid #0BA062;
    color: #0BA062;
  }
  .luxury_good{
    border-bottom: 0.2rem solid transparent;
    color: #333333;
  }
  .luxury_good_active{
    border-bottom: 0.2rem solid  #0BA062;
    color:  #0BA062;
  }
  .europe_art_good{
    margin-right: 1.5rem;
    border-bottom: 0.2rem solid transparent;
    color: #333333;
  }
  .europe_art_good_active{
    margin-right: 1.5rem;
    border-bottom: 0.2rem solid #0BA062;
    color: #0BA062;
  }
</style>



