import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);
const store=new Vuex.Store({
   state:{
     currentTab:1,
     showFooter:true,
     addCarNum:0
   },
  mutations:{
     // 首页底部导航栏的切换
     switchTab(state,index){
       state.currentTab=index;
     },
     // 是否显示底部的导航栏
     showFooterTab(state,flag){
       state.showFooter=flag;
     },
    //加入购物车中的商品数量
    addShopCarNum(state,num){
       state.addCarNum=num
    }
  }
});

export default  store;



