<template>
<div class="detail_container">
   <div class="param">
     <p class="tiaomao" v-if="datas.goods_skuid">商品条码：{{datas.goods_skuid}}</p>
     <p class="guige">规格：100g</p>
     <div class="product_address">产地：马来西亚</div>
     <div class="unit">单位：盒</div>
   </div>
  <div class="middle_part">
    <span class="left_line"></span>
    <span class="smailier_good">类似商品</span>
    <span class="right_line"></span>
  </div>
  <div class="grid_container">
    <ul class="clearfix" >
      <li v-for="(item,index) in datas.recommendGoods" class="item" @click="goodDetail(datas.recommendGoods[index].goods_id)">
        <img  class="good_img" v-lazy="item.goods_image">
        <!--<img src="" class="good_img">-->
        <div class="desc">
          <p class="good_name">{{item.goods_name}}</p>
          <div class="num_price">
            <p class="good_price">￥{{item.goods_price}}</p>
          </div>
        </div>
      </li>
    </ul>
  </div>

</div>
</template>
<script>
  import router from '../../router/index'
  export default {
      props:{
        datas:{
          type:Object
        }
      },
    data(){
        return{
          goodId:''
        }
    },
    watch: {
      '$route' (to, from) {
        // 对路由变化作出响应...
        console.log("======to========")
        this.$emit("refreshEvent",this.goodId)
      }
      },
    methods:{
      goodDetail(goodId){
        const path='/gooddetail/'+goodId;
        router.push({ path: path })
        console.log("====goodId==="+goodId)
        this.goodId=goodId;
      }
    }
  }
</script>
<style scoped>
  @import "../../css/reset.css";
  @import "detail_goodlist.css";
</style>



