<!--用户登录页面-->
<template lang="html" xmlns:v-on="http://www.w3.org/1999/xhtml">
  <div class="login_container">
      <div class="logo">
         <span class="logo_login"></span>
      </div>
      <div class="phone">
       <span class="phone_icon"></span>
        <div class="phone_input">
          <input type="number" placeholder="请输入手机号" v-model="mobile" v-on:focus="inputFocus()">
        </div>
      </div>
      <div class="password">
        <span class="pwd_icon"></span>
        <div class="password_input">
          <input type="password" placeholder="请输入密码" v-model="pwd" v-on:focus="inputFocus()">
        </div>
      </div>
     <div class="login_btn" @click="login">登录</div>
     <div class="foget">
       <span @click="fogetPwd">忘记密码？</span>
     </div>
    <div class="register_visit">
       <div class="bottom_item">
         <span class="instant_regist" @click="register">立即注册</span>
         <span class="line"></span>
         <span class="visit" @click="visit">随便看看</span>
       </div>
    </div>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import Utils from '../../common/utils'
  export default {
    data(){
      return{
        mobile:'',
        pwd:'',
        screenHeight: document.body.clientHeight   // 这里是给到了一个默认值 （这个很重要）
      }
    },
    methods:{
      isShowNav(){
        this.$store.commit("showFooterTab",false);
      },
//      登录
      login(){
        let mobile =this.mobile.trim();
        let pwd =this.pwd.trim();

        if(!Utils.vaildPhone(mobile)){
          return;
        }
        if(pwd.length==0){
          Utils.ToastUtil('请输入密码');
          return;
        }
        this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=login&op=index", //跨域请求接口
          {
            params: {
              member_name:mobile,
              member_password:pwd
            },
            jsonp:'callback',
            jsonpcallback	:'login',
          }).then(function(res){
          const response=JSON.parse(res.bodyText);
          if(response.code===0){
            Utils.ToastUtil(response.msg)
            let token=response.datas.token.token;
            //将token保存在本地
            localStorage.setItem('token',token);
            this.$router.go(-1);
            this.mobile=''
            this.pwd=''

          }else{
            Utils.ToastUtil(response.msg)
          }

        },function(){
          Utils.ToastUtil("登录请求失败！！！")
        });
      },
     //忘记密码
      fogetPwd(){

      },
     //注册
      register(){
        this.$router.push({
          path:'/regist'
        });
      },
     //随便看看
      visit(){
       this.$router.go(-1);
      }
    },
    created(){
      this.isShowNav();
    },
    watch: {
      screenHeight (val) {
        Utils.ToastUtil("观察==="+val)
      }
    },
    mounted(){
      const that = this
      window.onresize =function ()  {
          window.screenHeight = document.body.clientHeight
          that.screenHeight = window.screenHeight
          Utils.ToastUtil("kaifshi==="+that.screenHeight)
      }
    }

  }
</script>
<style scoped lang="css">
  @import "../../css/reset.css";
  @import "user-login.css";
</style>



