<!--产品参数 和商品详情-->
<template lang="html">
   <div class="good_switch">
     <div class="product" @click="select(1)" :class="{'active':currentIndex===1}">
       <span>产品参数</span>
     </div>
     <span class="line"></span>
     <div class="good_detail" @click="select(2)" :class="{'active':currentIndex===2}">
       <span >商品详情</span>
     </div>
   </div>
</template>
<script>
  export default {
    props:{
      searchBarFixed:{
        type:Boolean
      }
    },
    data(){
      return{
        currentIndex:1
      }
    },
    methods:{
      select(index){
        this.currentIndex=index
        this.$emit("switchIndex",index)
      }
    }
  }
</script>
<style scoped lang="css">
  @import "../../css/reset.css";
  .good_switch{
    display:flex;
    width: 100%;
    height:50px;
    align-items: center;
  }
  .product{
    display: flex;
    flex: 1;
    height: 100%;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    border-bottom: 0.2rem solid white;
    font-weight: 400;
    font-size: 3rem;

  }
  .line{
    display: block;
    width: 0.1rem;
    height: 2rem;
    background-color: #cccccc;

  }
  .good_detail{
    display: flex;
    flex: 1;
    height: 100%;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    border-bottom: 0.2rem solid white;
    font-weight: 400;
    font-size: 3rem;
  }
  .active{
    border-bottom: 0.2rem solid #0ba062;
  }
</style>



