import Vue from 'vue'
import Router from 'vue-router'
import Category from '../pages/category/category.vue'
import Index from '../pages/index/index.vue'
import Shop from '../pages/shop/shop.vue'
import ShopCar from '../pages/shopcar/shopcar.vue'
import Mine from '../pages/mine/mine.vue'
import Search from '../pages/search/search.vue'
import UserLogin from '../pages/login/user-login.vue'
import UserRegist from '../pages/regist/regist.vue'
import GoodDetail from '../pages/good-detail/good-detail.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/index'
    }
    ,
    {
      path: '/category',
      name: 'category',
      component: Category
    }
    ,
    {
      path: '/index',
      name: 'index',
      component: Index
    }
    ,
    {
      path: '/shop',
      name: 'shop',
      component: Shop
    }
    ,
    {
      path: '/shopCar',
      name: 'shopCar',
      component: ShopCar
    }
    ,
    {
      path: '/mine',
      name: 'mine',
      component: Mine
    }
    ,
    {
      path: '/search',
      name: 'search',
      component: Search
    },
    {
      path: '/login',
      name: 'login',
      component: UserLogin
    },
    {
      path: '/regist',
      name: 'regist',
      component: UserRegist
    },
    {
      path: '/gooddetail/:id',
      name: 'gooddetail',
      component: GoodDetail
    }
  ]
})



