<template>
  <div class="index_container">
    <!--顶部search-->
    <div class="search_container" @click="search">
       <div class="search">
         <div class="input_indicator">
           <span class="indicator_content">商品、店铺</span>
         </div>
         <span class="search_icon"></span>
       </div>

    </div>
    <!--banner-->
    <div class="banner_container">
      <mt-swipe :auto="4000" @change="handleChange">
        <mt-swipe-item  v-for="item in datas.ad_list">
          <img :src="item.img" alt="" class="banner_img" >
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <!--超市，奢侈品，欧洲艺术品-->
    <div class="category_container">
      <ul>
        <li class="market_item" >
          <img src="http://gbfmall.slh8.com/data/upload/shop/common/category-pic-1.jpg">
          <p>进口超市批发</p>
        </li>
        <li class="market_item" >
          <img src="http://gbfmall.slh8.com/data/upload/shop/common/category-pic-2.jpg">
          <p>奢侈品</p>
        </li>
        <li class="market_item" >
          <img src="http://gbfmall.slh8.com/data/upload/shop/common/category-pic-3.jpg">
          <p>欧洲艺术品</p>
        </li>
      </ul>
    </div>
    <!--广告-->
    <div class="adv">
      <mt-swipe :auto="4000"  :show-indicators="false">
        <mt-swipe-item  v-for="item in datas.adv_list">
          <img :src="item.img" alt="" class="banner_img" >
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <!--商品列表-->
    <div>
      <grid-view :goodlist="goodList" @upload="uploadMore" @seeDetail="goodDetail"></grid-view>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import GridView from '../../components/grid-list'
  import store from '../../store/store'
  import router from '../../router/index'
  import { Toast } from 'mint-ui';
  export default {
      data(){
        return{
          datas:{},
          goodList:[]
        }
      },
    components:{
      GridView
    },
    methods:{
//        商品详情
      goodDetail(goodId){
        //不显示底部导航栏
        this.$store.commit("showFooterTab",false);
      const path='/gooddetail/'+goodId;
        router.push({ path: path })
        console.log("====goodId==="+goodId)
        //    hide底部导航栏
      },
//        搜索
      search(){
        this.$store.commit("showFooterTab",false);
        router.push({ path: '/search' })

      },
    //      banner点击事件
      handleChange(index) {
//        console.log('item   '+index)
      },
    // 上拉加载更多数据
      uploadMore(currentPage){
       this.getHomeMoreData(currentPage)
      },
  //  luxury_life 首页数据
  getHomeData(){
        var that=this;
    this.$http.jsonp("http://gbfmall.slh8.com/mobile/index.php?act=index&op=home",
      {
        jsonp:'callback',
        jsonpcallback	:'home',
      }).then(function(res){
      that.datas=JSON.parse(res.bodyText).datas;
      that.goodList=JSON.parse(res.bodyText).datas.goods_list;
    },function(){
      Toast("首页数据请求失败！！！")
    });

  },
      //首页上拉刷新，下拉加载
      getHomeMoreData(currentpage){
//      let token=localStorage.getItem('token');
        let token='';
        let that=this;
        this.$http.jsonp('http://gbfmall.slh8.com/mobile/index.php?act=index&op=goodsListOnHome&token='+token,
          {
            params: {
              curpage:currentpage,

            },
            jsonp:'callback',
            jsonpcallback	:'collectShop',
          }).then(function(res){
          let dataList=JSON.parse(res.bodyText).datas.goods_list;
          let datas = JSON.parse(res.bodyText).datas;
          console.log("=====是否是数组===code======"+JSON.parse(res.bodyText).datas.constructor==Object)
          if(datas.goods_list=== undefined){//空数组，表示已经没有更多的数据了
          }else{
            let dataList=JSON.parse(res.bodyText).datas.goods_list;
            for(let  i=0;i<dataList.length;i++){
              that.goodList.push(dataList[i]);
            }
          }

          this.loading = false;
        },function(){
          Toast("首页数据请求失败！！！")
        });
      }
    },
    created(){
        //显示底部导航栏
      this.$store.commit("showFooterTab",true);
      //首页
      this.getHomeData();
      //登录
//      this.login();

    },
    destroyed(){
//      console.log("=====index=======destroyed==")
    }
  }
</script>
<style >
@import "../../css/reset.css";
.index_container{
  background-color: #f9f9f9;
}
  .search_container{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 50px;
    background-color: #0BA062;
    align-items: center;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 3;
  }
  .search{
    position: relative;
    display: flex;
    width: 60%;
    height: 30px;
    margin: 0 auto;
    background-color: white;
    border: 0.1rem solid white;
    border-radius: 5rem;
    align-items: center;
    box-sizing: border-box;
    padding-left: 1rem;
    justify-content: space-between;


  }
  .input_indicator{
   display: table;
  }
  .indicator_content{
    font-size: 12px;
    display: table-cell;
    vertical-align: middle;
  }
  .search_icon{
    width: 2rem;
    height: 2rem;
    background-image: url("../../assets/images/search_icon.png");
    background-repeat: no-repeat;
    background-size: 2rem 2rem;
    margin-right: 1.5rem;
  }
  .banner_container{
    margin-top: 4.3rem;
    width: 100%;

    height: 200px;
  }
  .banner_img{
    display: block;
    width: 100%;

  }
  .category_container{
    width: 100%;
    height: 12rem;
    margin-bottom: 0.5rem;
    background-color: #fff;

  }
  .category_container ul{
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: space-around;
  }
.category_container ul li{
  width: 33.3%;
  height:100%;
  box-sizing: border-box;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}
.category_container ul li img{
  display: block;
  width: 60px;
  height: 60px;
  background: white;
}
.category_container ul li p{
  display: flex;
  justify-content: center;
  align-items: center;
  color: #333333;
  font-size: 2rem;
  background: white;
}
  .adv{
    width: 100%;
    height: 200px;
  }
.index_container::-webkit-scrollbar{display: none;}
</style>



