<template>
  <div class="mine_container">
    <!--top-->
   <div class="search_container">
     <div class="input_label">
       <input type="search" placeholder="请输入商品或店铺名称">
       <span class="search_icon"></span>
     </div>
   </div>

    <div class="content">
    <div class="nav_tab">
    <nav-switch @selectTab="selectIndex"></nav-switch>
    </div>

    <div class="good_content">
      <div class="left">
        <ul>
          <li class="left_category"
              v-for="(item, index) in leftList"
              :class="(currentTab===index)? 'left_category_active' : 'left_category'"
              @click="clickLeft(index)"
          >{{item}}</li>
        </ul>
      </div>

      <div class="right">
        <div class="brand_cover">
          <img src="http://img4.imgtn.bdimg.com/it/u=486460003,4217634595&fm=27&gp=0.jpg" alt="">
        </div>

        <div class="grid">
          <ul class="clearfix">
            <li class="good_item" v-for="(item , index) in goodList">
              <img :src="item.goods_image" class="good_img">
              <p class="good_name">{{item.goods_name}}</p>
            </li>

          </ul>
        </div>

      </div>
    </div>

    </div>

  </div>
</template>
<script>
  import HeaderView from '../../components/header-view'
  import NavSwitch from '../../components/nav_switch'
  export default {
    data(){
      return{
        currentTab:1,
        leftList:['品牌','食品','红酒洋酒','鲜花','包装','电子产品'],
        goodList:[]
      }
    },
    methods:{
      //选项卡切换
      selectIndex(index){
        console.log("当前选择了那一个"+index)
      },
      //左侧选项
      clickLeft(index){
        this.currentTab=index;
      },
      //商品分类
      getGoodClass(){
        let token='';
        let that=this;
        this.$http.jsonp('http://gbfmall.slh8.com/mobile/index.php?act=goods&op=goods_list&token='+token,
          {
            jsonp:'callback',
            jsonpcallback	:'cataTwo',
          }).then(function(res){
            console.log("=======分类======"+JSON.parse(res.bodyText));
          that.datas=JSON.parse(res.bodyText).datas;
          that.goodList=JSON.parse(res.bodyText).datas.goodsList;

        },function(){
          console.log("首页数据请求失败！！！")
        });
      }
    },

    components:{
      HeaderView,
      NavSwitch
    },
    created(){
      //显示底部导航栏
      this.$store.commit("showFooterTab",true);
      this.getGoodClass();
    }
  }
</script>
<style lang="css" scoped>
@import "../../css/reset.css";
.mine_container{
  background-color: #f9f9f9;
}
.search_container{
  display: flex;
  width: 100%;
  height: 50px;
  background-color: #0BA062;
  align-items: center;
  justify-content: center;
  position: fixed;
  top:0;
  left:0;
  z-index: 999;
}
  .input_label{
    width: 65%;
    height: 36px;
    position: relative;
  }
.input_label input{
  display: inline-block;
  width: 100%;
  height:100%;
  background-color: white;
  border: 0.1rem solid white;
  border-radius: 5rem;
  padding: 0 1rem;
  font-family: "SimSun", "宋体";
  color: #333333;
  font-size: 1.8rem;
}
.search_icon{
  position: absolute;
  display: inline-block;
  width: 20px;
  height: 20px;
  background-image: url("../../assets/images/search_icon.png");
  background-repeat: no-repeat;
  background-size: 20px 20px;
  top: 8px;
  right:1rem;
}
.content{
  position: relative;
}
.nav_tab{
  width: 100%;
  margin-top: 51px;
  margin-bottom: 1rem;
  position: fixed;
  left: 0;
  top: 0rem;
  z-index: 3;
}
  .good_content{
    display: flex;
    width: 100%;
    margin-top: 4rem;
    margin-bottom: 6rem;
    background-color: skyblue;
    position: relative;
  }
  .left{
    width: 26.5%;
    height: 100%;
    background-color: white;
    position:fixed;
    top: 50px;
    left: 0;
    -webkit-overflow-scrolling: touch;
    overflow-scrolling: touch;
    overflow-y: scroll;
  }
  .left_category{
    display: flex;
    width: 100%;
    height:50px;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-bottom: 0.2rem solid #f9f9f9;
    color: #333333;
    font-size: 16px;
  }
.left_category_active{
  display: flex;
  width: 100%;
  height:50px;
  align-items: center;
  justify-content: center;
  border-bottom: 0.1rem solid #f9f9f9;
  background-color: #EFFFDA;
  font-size: 16px;
}
  .right{
    width: 73.5%;
    position: absolute;
    background-color: white;
    left:26.5%;
    top:46px;

  }
  .brand_cover{
    width: 100%;

  }
.brand_cover img{
  display: block;
  width:100%;
  height: 200px;
}
  .clearfix:after{
    display: block;
    content: '.';
    height:0;
    clear: both;
    visibility: hidden;
  }
  .good_item{
    float: left;
    width: 33.3%;
    height: 130px;
    box-sizing: border-box;
  }
  .good_img{
    width: 100px;
  }
  .good_name{
    width: 100%;
    box-sizing: border-box;
    padding: 0.3rem;
    text-align: center;
    height: 20px;
    line-height: 20px;
    color: #333333;
    background-color: #EFFFDA;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 14px;
  }
.good_item:nth-of-type(odd){

}
.good_item:nth-of-type(even){
}
</style>



